import { useEffect, useRef } from "react";
import gsap from "gsap";
import ScrollTrigger from "gsap/ScrollTrigger";
import ctaImg from "../assets/open-day-hero-bw.jpg";

gsap.registerPlugin(ScrollTrigger);

function PinnedCtaSection() {
  const sectionRef = useRef(null);
  const boxRef = useRef(null);

  useEffect(() => {
    const section = sectionRef.current;
    const box = boxRef.current;
  
    const boxMaxY = section.offsetHeight - box.offsetHeight;
  
    const ctx = gsap.context(() => {
      // 👇 POMERAJ .floated-box
      gsap.to(box, {
        y: boxMaxY,
        ease: "none",
        scrollTrigger: {
          trigger: section,
          start: "top top",
          end: `+=${boxMaxY}`,
          scrub: true,
        },
      });
  
      // 👇 PIN CELE SEKCIJE DO KRAJA
      ScrollTrigger.create({
        trigger: section,
        start: "top top",
        end: "bottom top", // pin traje dok sekcija ne izađe iz viewporta
        pin: true,
        pinSpacing: true,
      });
    }, sectionRef);
  
    return () => ctx.revert();
  }, []);

  return (
    <section className="section-7" ref={sectionRef}>
      <h2>At the heart of the thriving</h2>
      <img src={ctaImg} alt="" className="kids-img" />
      <div className="text">
      Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries, but also the leap into electronic typesetting, remaining essentially unchanged.
      </div>
      <div className="btn-box">
        <div className="floated-box" ref={boxRef}></div>
        
            <div className="btn-cta"><a href="#"><span>Prijavi se</span></a></div>
        
        
      </div>
    </section>
  );
}

export default PinnedCtaSection;
